.profilText {
    height: 700px;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &--title {
        font-family: 'Kaisei Decol', serif;
        font-size: 48px;
        margin: 75px 0 75px 50px;
        span {
            font-size: 60px;
            margin-left: 70px;
        }
    }
    &--presentation {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 80%;
        margin: 0 auto;
        padding: 10px 0;
        i {
            position: absolute;
        }
        .fa-quote-left {
            left: 0;
            top: 0;
        }
        
        .fa-quote-right {
            right: 0;
            bottom: 0;
        }
        &--text {
            font-family: 'BioRhyme', serif;
            width: 90%;
            text-align: justify;
        }
    }
}