.containerArticle {
    margin: 70px 0;
    position: relative;
    &--btnReturn {
        font-size: 30px;
        position: absolute;
        top: 10px;
        left: 50px;
    }
    &--title {
        text-align: center;
        font-weight: 700;
        font-size: 2rem;
        margin-bottom: 40px;
    }
    p:not(.text-insta) {
        text-align: justify;
        margin: 20px;
        font-size: 1.2rem;
    }
    .add {
        padding-top: 50px;
    }
    &--img {
    width: 400px;
    &:nth-last-child(8) {
        float: right;
        width: 300px;
        margin: 30px;
    }
    &:nth-last-child(2) {
        margin: 20px 70px;
    }
    }

    &--insta {
        width: 550px;
        height: 200px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        float: right;
        margin: 120px;
        h4 {
            font-size: 1.5rem;
            font-weight: 700;
            text-align: center;
        }
        a {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            width: 100%;
            div {
                display: flex;
                background-color: #FFF;
                width: 150px;
                height: 50px;
                border-radius: 15px;
            }
            .insta {
                width: 80px;
                height: 80px;
            }
            .instalogo {
                width: fit-content;
                border-radius: 15px 0 0 15px;
            }
            .text-insta {
                margin: auto;
            }
        }
    }
    video {
        float: right;
        width: 400px;
        margin: 30px;
    }
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    img {
        max-width: 400px;
        margin: 10px;
    }
}