.LinkedinCard {
    width: 300px;
    height: 450px;
    background-color: #FFF;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.LinkedinCard--banner {
    height: 150px;
    object-fit: cover;
}

.LinkedinCard--imgProfil {
    border-radius: 50%;
    position: absolute;
    top: 50px;
    border: 3px solid #FFF;
}

.LinkedinCard--content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100%;
    padding: 60px 30px 10px 30px;
}

.LinkedinCard--content--name {
    font-size: 1.5rem;
    color: rgb(83, 80, 80);
}

.LinkedinCard--content--bio {
    color: rgb(85, 83, 83);
    text-align: center;
}

.LinkedinCard--content--locat {
    color: rgb(85, 83, 83);
    font-weight: 300;
}

.LinkedinCard--content--work {
    font-size: 0.9rem;
    color: rgb(147, 141, 141);
    font-weight: 300;
}

.LinkedinCard--content--link {
    color: #4089d3;
    border: #4089d3 2px solid;
    padding: 7px;
    border-radius: 5px;
    font-weight: 500;
    text-decoration: none;
}

.LinkedinCard--content--logo {
    width: 100px;
}
