.navHome {
    margin-top: 150px;
    &--list {
        display: flex;
        justify-content: center;
    }
    a {
        margin: 0 20px;
        text-decoration: none;
        font-weight: 500;
        font-size: 23px;
        background-color: #75a2be;
        color: #FFF;
        border: #75a2be 2px solid;
        border-radius: 25px;
        padding: 10px 20px;
    }
    .activeLink {
        background-color: #FFF;
        color: #75a2be;
    }
}

main {
    max-width: 1400px;
    margin: 0 auto;
}
