.containerArticleCard {
    max-width: 400px;
    width: fit-content;
    height: fit-content;
    background-color: #FFF;
    padding-bottom: 10px;
    margin: 20px;
    h3 {
        font-weight: 700;
        font-size: 1.8rem;
        margin: 10px 25px;
    }
    p {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin: 10px 25px;
    }
    img {
        width: 100%;
    }
}
