.nav-parcours {
    text-align: center;
    margin-top: 80px;
}

.nav-parcours :any-link {
    text-decoration: none;
    color: #000;
    margin: 15px;
    border-top: #75a2be 3px solid;
    padding: 15px;
    background-color: #FFF;
    border-radius: 0 0 15px 15px;
}

.nav-parcours .catActive {
    background-color: #ffffff9b;
}

.nav-parcours :any-link:hover {
    background-color: #ffffff60;
    font-weight: bold;
}

.navLinkParcours {
    background-color: #ffffff60;
    font-weight: bold;
}

// content parcours

.container-parcours-content {
    margin: 70px 0;
    padding: 0 50px;
    display: flex;
    flex-direction: column;
}

.container-parcours-content h2 {
    font-weight: 700;
    font-size: 2rem;
    text-align: center;
}
x
.no-h3 {
    margin-bottom: 25px;
}

.container-parcours-content h3 {
    font-weight: 700;
    font-size: 1.3rem;
    margin: 25px 0;
}

.container-parcours-content h4 {
    font-weight: 500;
    margin: 15px 0;
}

.title-center {
    text-align: center;
}

.container-parcours-content p {
    text-align: justify;
    margin: 7px 0 7px 10px;
}

.container-parcours-content img, .container-parcours-content figure {
    width: 500px;
    margin: 25px auto;
}

.container-parcours-content figure {
    text-align: center;
}

.container-parcours-content ul {
    margin: 7px 0 7px 70px;
}

.container-parcours-content li {
    list-style-type: circle;
}