.container-contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 70px 0;
}

.container-contact h2 {
    font-weight: 700;
    font-size: 2rem;
    margin-bottom: 40px;
}

.containerContact {
    display: flex;
    justify-content: space-evenly;
    width: 1000px;
}

.mail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h5 {
        font-size: 2rem;
        margin: 20px;
    }
    a {
        background-color: #FFF;
        text-align: center;
        padding: 20px;
        border-radius: 15px;
        font-size: 20px;
        color: #7dadbb;
        width: 200px;
    }
}