.profilPicture {
    &--decoration {
        width: 655px;
        height: 405px;
        background-color: #FFF;
        rotate: -53deg;
        position: absolute;
        top: 100px;
        right: -100px;
        border-radius: 0 0 0 100px;
    }
    &--image {
        width: 633px;
        height: 633px;
        border-radius: 50%;
        z-index: 1;
        position: absolute;
        top: 72px;
        right: 109px;
    }
}

@media screen and (max-width: 780px) {
    .profilPicture {
        &--decoration {
            right: -200px;
        }
        &--image {
            right: 09px;
        }
    }
}

