.navBar {
    font-family: 'Lobster', cursive;
    background-color: #FFF;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    z-index: 5;
    &--login {
        margin-left: 50px;
    }
    &--list {
        font-size: 2rem;
        width: 50%;
        display: flex;
        justify-content: space-evenly;
    }
    &--logo {
        width: 250px;
        height: 45px;
        margin: 0 20px;
    }
    &--btn-mobile {
        font-size: 25px;
        background-color: #FFF;
        border: none;
        display: none;
        position: absolute;
        top: 25px;
        right: 40px;
    }
}

@media screen and (max-width: 780px) {
    .navBar {
        height: fit-content;
        flex-direction: column-reverse;
        justify-content: flex-end;
        &--login {
            display: none;
        }
        &--list {
            margin-top: 20px;
            flex-direction: column;
            align-items: center;
            &--item {
                border-top: 1px solid #000;
                padding: 5px 60%;
                margin: 5px 0;
            }
        }
        &--logo {
            margin: 15px 0;
        }
        &--btn-mobile {
            display: block;

        }
    }
}